  /**
  * Shuffles any array and returns it
  * @param {*any} a any array
  * @returns shuffled array
  */
const shuffle = (a) => {
  for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
  }
  return a;
}

export default shuffle;